@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
* {
  box-sizing: border-box; }

body {
  background-color: #f0f0f0;
  padding: 0;
  margin: 0;
  font-family: 'Nunito', sans-serif; }

.header {
  width: 100%;
  height: 60px;
  background-color: #f0f0f0;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 30; }
  @media (max-width: 768px) {
    .header {
      padding: 0 4px; } }
  .header__searchBox {
    display: flex;
    place-items: center;
    position: relative; }
    .header__searchBox__input {
      width: 0;
      opacity: 0;
      height: 40px;
      border: 1px solid #b4b2b2;
      border-radius: 20px;
      outline: none;
      padding: 0 5px 0 45px;
      -webkit-transition: width .5s ease, opacity 0.25s;
      transition: width .5s ease, opacity 0.25s;
      /*@media (max-width: 768px){
                width: 100px;
            }*/ }
      .header__searchBox__input--active {
        width: 250px;
        opacity: 1; }
      .header__searchBox__input::-webkit-input-placeholder {
        color: #c9c9c9; }
      .header__searchBox__input::-moz-placeholder {
        color: #c9c9c9; }
      .header__searchBox__input:-ms-input-placeholder {
        color: #c9c9c9; }
      .header__searchBox__input::-ms-input-placeholder {
        color: #c9c9c9; }
      .header__searchBox__input::placeholder {
        color: #c9c9c9; }
    .header__searchBox__button {
      width: 40px;
      height: 40px;
      background-color: #000;
      padding: 6px;
      border: none;
      border-radius: 20px;
      position: absolute;
      z-index: 10; }
      .header__searchBox__button:hover {
        cursor: pointer; }
  .header__cartIcon {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    background-color: #000;
    padding: 6px; }
    .header__cartIcon--active {
      background-color: #fff; }
    .header__cartIcon:hover {
      cursor: pointer; }
  .header__box {
    display: flex;
    place-items: center;
    position: relative; }
  .header__cartQuantity {
    width: 20px;
    height: 20px;
    background-color: #f45905;
    border-radius: 10px;
    font-size: 1em;
    text-align: center;
    position: absolute;
    color: #fff;
    right: 0;
    top: 20px; }
    .header__cartQuantity--active {
      color: #000; }

.shoppingCart {
  width: 30vw;
  min-height: 50px;
  max-height: 447px;
  position: absolute;
  top: 60px;
  right: 0;
  background-color: #fff;
  z-index: 40;
  border: 1px solid #b4b2b2;
  text-align: center;
  display: flex;
  flex-direction: column; }
  @media (max-width: 768px) {
    .shoppingCart {
      width: 100vw;
      justify-content: space-between; } }
  .shoppingCart__helpBox {
    overflow-y: scroll; }
  .shoppingCart__item {
    width: 100%;
    display: grid;
    grid-template-columns: 64px 1fr 1fr 50px;
    grid-template-rows: 64px 64px;
    grid-template-areas: "image name name deleteBtn" "image quantity price deleteBtn";
    padding: 10px 5px;
    border-bottom: 1px solid #c4c4c4; }
    .shoppingCart__item:last-child {
      border: none; }
    .shoppingCart__item__image {
      width: 64px;
      height: 128px;
      grid-area: image; }
    .shoppingCart__item__name {
      grid-area: name; }
    .shoppingCart__item__quantity {
      grid-area: quantity; }
    .shoppingCart__item__price {
      grid-area: price; }
    .shoppingCart__item__deleteBtn {
      width: 30px;
      border: none;
      background-color: transparent;
      align-self: center;
      font-size: 20px;
      grid-area: deleteBtn; }
      .shoppingCart__item__deleteBtn:hover {
        color: #ff0000;
        cursor: pointer; }
  .shoppingCart__summary {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #b4b2b2;
    padding: 5px 10px; }
    .shoppingCart__summary__total {
      font-size: 1em;
      font-weight: 700; }
    .shoppingCart__summary__checkout {
      width: 100px;
      height: 40px;
      background-color: #b62b65;
      border: none;
      color: #fff;
      font-size: 1.1em;
      border-radius: 10px; }
      .shoppingCart__summary__checkout:hover {
        cursor: pointer;
        background-color: #912250; }
      .shoppingCart__summary__checkout:focus {
        outline: 2px solid #3490de;
        outline-offset: 1px; }

.productList {
  width: 100%;
  height: calc(100vh - 60px);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: stretch;
  grid-gap: 10px 10px;
  overflow-y: scroll;
  padding: 10px; }
  @media (max-width: 768px) {
    .productList {
      grid-template-columns: 1fr 1fr; } }

.product {
  width: auto;
  height: 500px;
  background-color: #fff;
  border-radius: 2.5%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
  text-align: center;
  padding: 10px;
  position: relative; }
  @media (max-width: 768px) {
    .product {
      height: 350px; } }
  .product__image {
    width: 128px;
    height: 256px; }
    @media (max-width: 768px) {
      .product__image {
        width: 64px;
        height: 128px; } }
  .product__info {
    position: absolute;
    right: 10px;
    z-index: 20; }
  .product__name {
    width: 100%;
    font-size: 1.25em;
    font-weight: 700;
    margin: 10px 0; }
    @media (max-width: 768px) {
      .product__name {
        font-size: 0.9em; } }
    .product__name--smallCaption {
      font-size: 1em; }
      @media (max-width: 768px) {
        .product__name--smallCaption {
          font-size: 0.6em; } }
  .product__price {
    width: 100%;
    margin: 10px 0; }
    @media (max-width: 768px) {
      .product__price {
        font-size: 0.9em; } }
  .product__quantity {
    width: 100%;
    margin: 10px 0 20px 0;
    display: flex;
    justify-content: center;
    align-content: center; }
    .product__quantity__button {
      width: 30px;
      height: 30px;
      background-color: #ed6663;
      color: #fff;
      border: none;
      font-size: 1.25em; }
      .product__quantity__button:hover {
        cursor: pointer;
        background-color: #ea4b48; }
      .product__quantity__button:first-child {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px; }
      .product__quantity__button:last-child {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px; }
    .product__quantity__input {
      width: 40px;
      height: 30px;
      text-align: center;
      border: none;
      font-size: 1.05em; }
  .product__button {
    width: 120px;
    height: 40px;
    padding: 5px;
    border: none;
    background-color: #b62b65;
    color: #fff;
    font-size: 1.1em;
    border-radius: 10px; }
    .product__button:hover {
      cursor: pointer;
      background-color: #912250; }
    .product__button:focus {
      outline: 2px solid #3490de;
      outline-offset: 1px; }
  .product__description {
    width: 100%;
    height: 100%;
    background-color: #fff8f8;
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    padding: 10px; }

.messegeBox {
  width: 300px;
  padding: 5px;
  background-color: #fff;
  border: 2px solid #b62b65;
  border-radius: 10px;
  box-shadow: 0 0 2px 0 #000;
  position: absolute;
  left: 50%;
  top: 5%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 100;
  text-align: center; }

